// src/components/charts/UsageChart.tsx - Updated for dark mode
'use client';

import { useTheme } from 'next-themes';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Area, AreaChart } from 'recharts';

const data = [
    { date: 'Jan 1', requests: 4000, revenue: 2400 },
    { date: 'Jan 2', requests: 3000, revenue: 1398 },
    { date: 'Jan 3', requests: 2000, revenue: 9800 },
    { date: 'Jan 4', requests: 2780, revenue: 3908 },
    { date: 'Jan 5', requests: 1890, revenue: 4800 },
    { date: 'Jan 6', requests: 2390, revenue: 3800 },
    { date: 'Jan 7', requests: 3490, revenue: 4300 },
];

export function UsageChart() {
    const { theme } = useTheme();

    const isDark = theme === 'dark' || theme === 'modern';
    const textColor = isDark ? '#a1a1aa' : '#71717a';
    const gridColor = isDark ? '#27272a' : '#e4e4e7';
    const tooltipBg = isDark ? '#27272a' : '#ffffff';
    const tooltipBorder = isDark ? '#3f3f46' : '#e4e4e7';

    return (
        <div className="h-[300px] w-full">
            <ResponsiveContainer width="100%" height="100%">
                <AreaChart data={data} margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
                    <defs>
                        <linearGradient id="colorRequests" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#3b82f6" stopOpacity={0.8} />
                            <stop offset="95%" stopColor="#3b82f6" stopOpacity={0} />
                        </linearGradient>
                        <linearGradient id="colorRevenue" x1="0" y1="0" x2="0" y2="1">
                            <stop offset="5%" stopColor="#10b981" stopOpacity={0.8} />
                            <stop offset="95%" stopColor="#10b981" stopOpacity={0} />
                        </linearGradient>
                    </defs>
                    <CartesianGrid strokeDasharray="3 3" stroke={gridColor} />
                    <XAxis
                        dataKey="date"
                        axisLine={false}
                        tickLine={false}
                        tick={{ fill: textColor, fontSize: 12 }}
                    />
                    <YAxis
                        axisLine={false}
                        tickLine={false}
                        tick={{ fill: textColor, fontSize: 12 }}
                    />
                    <Tooltip
                        contentStyle={{
                            backgroundColor: tooltipBg,
                            borderColor: tooltipBorder,
                            borderRadius: '0.75rem',
                            boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1)',
                            color: isDark ? '#ffffff' : '#000000',
                        }}
                    />
                    <Area
                        type="monotone"
                        dataKey="requests"
                        stroke="#3b82f6"
                        fillOpacity={1}
                        fill="url(#colorRequests)"
                        strokeWidth={2}
                    />
                    <Area
                        type="monotone"
                        dataKey="revenue"
                        stroke="#10b981"
                        fillOpacity={1}
                        fill="url(#colorRevenue)"
                        strokeWidth={2}
                    />
                </AreaChart>
            </ResponsiveContainer>
            <div className="flex justify-center space-x-6 mt-4">
                <div className="flex items-center">
                    <div className="h-3 w-3 bg-blue-500 rounded-full mr-2"></div>
                    <span className="text-sm text-default-500">USSD Requests</span>
                </div>
                <div className="flex items-center">
                    <div className="h-3 w-3 bg-green-500 rounded-full mr-2"></div>
                    <span className="text-sm text-default-500">Revenue ($)</span>
                </div>
            </div>
        </div>
    );
}